.hero-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 12rem;
    padding-bottom:10rem;
    padding-inline: 20%;
}

.hero-container h1 {
    font-size: 7rem;
    margin: 0;
    background: linear-gradient(90deg, rgba(94,94,99,1) 0%, rgba(76,76,147,1) 58%, rgba(25,144,168,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: center;
}

.hero-container span {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    padding-bottom: 2rem;
}

.hero-container img {
    height: 1.5rem;
    width: 1.5rem;
}

.hero-container p {
    text-align: center;
    margin-block: 0;
    width: 85%;
}

@media only screen and (max-width: 600px) {
    .hero-container {
       padding-top: 0;
       padding-inline: 1rem;
    }

    .hero-container h1 {
        font-size: 6rem;
    }
  }